header .wrapper {
  padding: 0;
}

header nav {
  margin-top: 40px;
  position: absolute;
  z-index: 1;
  width: 100%;
  max-width: 1200px;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;
  padding: 0 15px;
}

header nav .nav ul {
  display: flex;
  width: 595px;
  justify-content: space-between;
}

header nav .nav ul li:before {
  content: "";
  display: block;
  position: relative;
  width: 7px;
  height: 7px;
  border: 1px solid #fff;
  right: 20px;
  top: 50%;
  transform: translateY(-1px);
  border-radius: 50%;
}

header nav .nav ul li a {
  color: #fff;
  font-family: Gotham-Book,sans-serif;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none;
}

header nav .nav ul li:hover:before {
  background-color: #fff;
}

header nav .social {
  display: flex;
  justify-content: center;
  align-items: center;
}

header nav .social a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 41px;
  height: 40px;
  border: 1px solid #fff;
  border-radius: 50%;
  color: #fff;
  font-size: 16px;
  text-decoration: none;
  transition: all 0.3s linear;
}

header nav .social a:not(:first-child) {
  margin-left: 20px;
}

header nav .social a:hover {
  transform: scale(1.2);
  box-shadow: 0px 0px 10px #fff;
}

header .slider__item {
  background: url("../img/header/header_bg.png"), linear-gradient(45deg, #55b7ff 0%, #7e5aff 100%);
  background-size: cover;
  color: #fff;
}

header .slider__item--desc {
  height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

header .slider__item--desc h1 {
  font-family: Gotham,sans-serif;
  font-size: 75px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 4px;
  margin-bottom: 39px;
  text-align: center;
}

header .slider__item--desc p {
  text-align: center;
  font-family: GothamBook, sans-serif;
  font-size: 18px;
  font-weight: 400;
  text-transform: uppercase;
  letter-spacing: 5.4px;
}

header .down {
  position: absolute;
  bottom: 30px;
  left: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 40px;
  height: 40px;
  border: 1px solid #fff;
  border-radius: 50%;
  cursor: pointer;
  text-decoration: none;
  transition: all 0.3s linear;
}

header .down:hover {
  transform: scale(1.2);
  box-shadow: 0px 0px 10px #fff;
}

header .down i {
  color: #fff;
}

ul.slick-dots {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: flex-end;
  bottom: 50%;
  transform: translateY(50%);
}

ul.slick-dots li {
  border: 1px solid white;
  border-radius: 50%;
  width: 9px;
  height: 9px;
  margin: 20px;
}

ul.slick-dots li button {
  display: none;
}

ul.slick-dots .slick-active {
  background-color: #fff;
}

.navMobile {
  display: none;
}

.navMobile .hamb {
  width: 37px;
  height: 37px;
  cursor: pointer;
  position: relative;
  left: -15px;
  z-index: 99;
}

.navMobile .hamb span {
  display: inline-block;
  width: 100%;
  border-top: 2px solid #fff;
  position: relative;
  transition: all 0.3s linear;
}

.navMobile .hamb span:before,
.navMobile .hamb span:after {
  transition: all 0.3s linear;
  content: "";
  border-top: 2px solid #fff;
  position: absolute;
  width: 100%;
}

.navMobile .hamb span:before {
  top: -12px;
}

.navMobile .hamb span:after {
  top: 8px;
}

.navMobile .hamb__active {
  position: fixed;
  left: unset;
  top: 50px;
  right: 30px;
}

.navMobile .hamb__active span {
  border: transparent;
}

.navMobile .hamb__active span:before {
  transform: rotate(135deg);
  top: 0;
}

.navMobile .hamb__active span:after {
  transform: rotate(-135deg);
  top: 0;
}

.navMobile .overlay {
  position: fixed;
  height: 100vh;
  top: 0;
  left: -100%;
  width: 100%;
  background-image: linear-gradient(45deg, #55b7ff 0%, #7e5aff 100%);
  z-index: 7;
  transition: all 0.3s linear;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.navMobile .overlay ul li {
  text-align: center;
  padding: 10px;
}

.navMobile .overlay ul li a {
  color: #fff;
  font-family: Gotham-Book,sans-serif;
  font-size: 14px;
  font-weight: 400;
  text-transform: uppercase;
  text-decoration: none;
}

.navMobile .overlay__active {
  left: 0;
}

.navMobile .socialMobile {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 50px;
}

.navMobile .socialMobile a {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 41px;
  height: 40px;
  border: 1px solid #fff;
  border-radius: 50%;
  color: #fff;
  font-size: 16px;
  text-decoration: none;
  transition: all 0.3s linear;
}

.navMobile .socialMobile a:not(:first-child) {
  margin-left: 20px;
}

.navMobile .socialMobile a:hover {
  transform: scale(1.2);
  box-shadow: 0px 0px 10px #fff;
}

